import styles from "./Main.module.css";
import search from "./img/search.png";
import { Helmet } from "react-helmet";

function Main({ onCharacterNameChange, onSubmit }) {
  const onChange = (e) => {
    const characterName = e.target.value;
    onCharacterNameChange(characterName);
  };
  return (
    <div className={styles.mainContainer}>
      <Helmet>
        <meta
          name="description"
          content="메이플스토리 캐릭터 정보 검색 페이지"
        />
        <meta
          name="keywords"
          content="홈, 웹사이트, 메이플스토리, 메이플 정보, 메이플, 메이플스토리 전적 검색"
        />
      </Helmet>
      <div className={styles.contentsBox}>
        <div className={styles.searchBar}>
          <form onSubmit={onSubmit}>
            <input
              className={styles.inputField}
              placeholder="애정가득한 캐릭터명을 입력해보세요."
              onChange={onChange}
            />
            {/* 추가: 폼 제출 시 fetchData 호출 */}
            <button type="submit" style={{ display: "none" }}></button>
          </form>
          <img
            className={styles.searchIcon}
            src={search}
            onClick={onSubmit}
            alt="검색 아이콘"
          ></img>
        </div>
      </div>
    </div>
  );
}

export default Main;
